.App-contact {
  border-width: 2px;
  border-style: solid;
  border-color: #272727;
  background-color: transparent;
  -webkit-transition: 0.1s background-color linear, 0.1s color linear;
  -o-transition: 0.1s background-color linear, 0.1s color linear;
  transition: 0.1s background-color linear, 0.1s color linear;
  padding: 21px 34px;
  font-family: promixa-nova;
  font-size: 15px;
  font-weight: 400px;
  text-transform: uppercase;
  letter-spacing: .1em;
  padding-left: 17px;
  padding-right: 17px;
  width: 150px;
  height:20px;
  margin: 0 auto;
  margin-top: 20px;

}

.App-video {
  padding-left: 17px;
  padding-right: 17px;
  margin-top: 30px;
