nav {
  background-color: #1A1A1A;
  overflow: hidden;
  padding: 1em;

  a {
    color: #ffffff;
  }

  a:visited {
    color: #ff0000;
  }

  border-bottom: 1px solid #000;

  .navDesktop {
    display: none;
    margin: 0 auto;

    .DesktopDiv {
      text-align: center;

      a {
        text-decoration: none;
        display: inline-block;
        // Adjust padding to increase/decrease the space between links.
        padding: 0 2em;
      }
    }
  }

  // Navbar is set to default with the "hamburger" and links on the left
  // side of the screen. To move to the right of the screen, simply
  // change the three "float: Left;" references to read "float: right;".
  .navMobile {

    i {
      float: left;
      cursor: pointer;
      color: #FFF;

    }

    .mobileLinks {
      display: none;

      a {
        text-decoration: none;
        display: block;
        float: left;
        clear: left;
        padding: 0.5em 0;
      }
    }
  }
}

